.fixed-plugin {
  .fixed-plugin-button {
    background: $white;
    border-radius: $fixed-plugin-radius;
    bottom: $fixed-plugin-bottom;
    right: $fixed-plugin-right;
    font-size: $font-size-xl;
    z-index: $fixed-plugin-button-z-index;
    box-shadow: $fixed-plugin-box-shadow;
    cursor: pointer;

    i {
      pointer-events: none;
    }
  }

  .card {
    position: fixed !important;
    right: -$fixed-plugin-card-width;
    top: 0;
    height: 100%;
    left: auto !important;
    transform: unset !important;
    width: $fixed-plugin-card-width;
    border-radius: 0;
    padding: 0 10px;
    transition: .2s ease;
    z-index: $fixed-plugin-card-z-index;
  }

  .badge {
    border: 1px solid $white;
    border-radius: 50%;
    cursor: pointer;
    display: inline-block;
    height: 23px;
    margin-right: 5px;
    position: relative;
    width: 23px;
    transition: $transition-base;

    &:hover,
    &.active {
      border-color: #00bbff;
    }
  }

  .btn.bg-gradient-dark:not(:disabled):not(.disabled) {
    border: 1px solid transparent;

    &:not(.active) {
      background-color: transparent;
      background-image: none;
      border: 3px solid $dark;
      color: $dark;
    }
  }

  &.show {
    .card {
      right: 0;
    }
  }
}

.fixed-plugin .background {
  display: block;
  padding: 18px 2px;
  width: 25%;
  float: left;
}

.fixed-plugin span img.img-holder {
  font-size: 16px;
  text-align: center;
  border-radius: 10px;
  background-color: #FFF;
  border: 3px solid #FFF;
  padding-left: 0;
  padding-right: 0;
  opacity: 1;
  cursor: pointer;
  display: block;
  max-height: 100px;
  overflow: hidden;
  padding: 0;
}

.fixed-plugin span:hover>img.img-holder,
.fixed-plugin span:focus>img.img-holder {
  border-color: rgba(0, 187, 255, 0.53);
}

.fixed-plugin span.active>img.img-holder,
.fixed-plugin span.active>img.img-holder {
  border-color: #00bbff;
  background-color: #FFFFFF;
}

.float-start {
  float: left !important;
}

.float-end {
  float: right !important;
}

.fixed-plugin .badge-black {
  background-color: #000;
}

.fixed-plugin .badge-azure {
  background-color: #2CA8FF;
}

.fixed-plugin .badge-green {
  background-color: #18ce0f;
}

.fixed-plugin .badge-orange {
  background-color: #f96332;
}

.fixed-plugin .badge-yellow {
  background-color: #FFB236;
}

.fixed-plugin .badge-danger {
  background-color: #f44336;
}

.fixed-plugin .badge-purple {
  background-color: #9368E9;
}

.fixed-plugin .badge-white {
  background-color: rgba(200, 200, 200, 0.2);
}

.fixed-plugin .badge-rose {
  background-color: #e91e63;
}

.fixed-plugin .badge-red {
  background-color: #f44336;
}